<template lang="html">
  <label class="check">
      <input class="checkbox" v-model="curValue" @change="$emit('change', curValue)" type="checkbox">
      <i class="check-icon"></i>
  </label>
</template>

<script>
export default {
  props: {
    value: {}
  },
  watch: {
    value (val) {
      this.curValue = val
    },
    curValue (val) {
      this.$emit('input', val)
    }
  },
  data () {
    return {
      curValue: this.value
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.check {
  width: .3rem;
  height: .3rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
label {
    font-size: .3rem;
    cursor: pointer;
}

label i {
    font-size: .3rem;
    font-style: normal;
    display: inline-block;
    width: .3rem;
    height: .3rem;
    text-align: center;
    line-height: .3rem;
    color: @whiteColor;
    vertical-align: middle;
    margin: -2px 2px 1px 0px;
    background: url('../../assets/img/angle/check01@2x.png');
    background-size: .3rem .3rem;
}
input[type="checkbox"] {
    display: none;
}

input[type="checkbox"]:checked+ i{
  background: url('../../assets/img/angle/check02@2x.png');
  background-size: .3rem .3rem;
}

input[type="checkbox"]:disabled+ i {
    border-color: @angleCheckDisabled;
}

input[type="checkbox"]:checked:disabled+ i{
    background: @angleCheckDisabled;
}

</style>



