<template lang="html">
  <div class="">
    <default-head header="天使福包"></default-head>
    <div id="wrapper" class="package-cnt-box">
      <nav-table-box>
        <nav-table-cell title="福包可用数量"
        cellclass="nav-table-cell--white" :number="666" numclass="f-redColor"></nav-table-cell>
        <nav-table-cell title="福包爱心总额"
        cellclass="nav-table-cell--white" :number="666" numclass="f-redColor"></nav-table-cell>
      </nav-table-box>
      <nav-table-box>
        <nav-table-cell @click.native="showForWipe = !showForWipe" title="兑入天使福包"
         height=".88rem" cellclass="nav-table-cell--red"></nav-table-cell>
        <nav-table-cell @click.native="showSendWipe = !showSendWipe" title="赠送福包"
          height=".88rem" cellclass="nav-table-cell--red"></nav-table-cell>
      </nav-table-box>
      <white-title lefttext="兑换记录"  righttext="排序" margin="0 0 0 .22rem" leftcolor="#333" rightcolor="#666"
      :isshow="true"></white-title>

      <div class="">
        <note-list @click.native="$router.push('/packagedetail')" topnum="70548"
        :hasselect="true" :hassort="true" time="2017-05-04 12:12:12" text="以500汽车爱心兑换爱心福包">
          <flm-check-box></flm-check-box>
        </note-list>
        <note-list @click.native="$router.push('/packagedetail')" topnum="1052876" :hasselect="true" :hassort="true" time="2017-05-04 12:12:12" text="以500汽车爱心兑换爱心福包">
          <flm-check-box></flm-check-box>
        </note-list>
      </div>

      <foot-swipe-block v-if="showForWipe" title="天使福包" @close-swipe="showForWipe = false">
        <div class="text-box" >
          <flm-form-input label="剩余天使爱心" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="今日剩余天使爱心" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="兑换天使福包名额" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="您当前可兑换的数量还剩" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input type="password" label="支付密码" placeholder="请输入支付密码" textAlign="right"></flm-form-input>
        </div>
        <div class="package-angel-notice">
          <p>兑换天使福包需要捐赠500个天使爱心</p>
        </div>
        <sub-button class="f-marginB40" text="确认捐赠" margintop=".5rem"></sub-button>
      </foot-swipe-block>

      <foot-swipe-block v-if="showSendWipe" title="赠送福包" @close-swipe="showSendWipe = false">
        <div class="text-box" >
          <flm-form-input label="福包爱心总额" textAlign="right" ></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="接收方账号" textAlign="right" ></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="支付密码" textAlign="right" type="password"></flm-form-input>
        </div>
        <sub-button class="f-marginB40" text="确认捐赠" margintop="0.5rem"></sub-button>
      </foot-swipe-block>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import navTableBox from '@/components/angle/navTableBox'
import navTableCell from '@/components/angle/navTableCell'
import whiteTitle from '@/components/angle/whiteTitle'
import noteList from '@/components/angle/noteList'
import flmCheckBox from '@/components/angle/checkBox'
import footSwipeBlock from '@/components/angle/footSwipeBlock'
import subButton from '@/components/common/subButton'
export default {
  components: { defaultHead, navTableBox, navTableCell, whiteTitle, noteList, flmCheckBox, footSwipeBlock, subButton },
  data () {
    return {
      showForWipe: false,
      showSendWipe: false
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.package-cnt-box {
  .text-box {
    background: @whiteColor;
    padding: 0 .22rem;
    border-bottom: 1px solid @lineColor;
  }
  .package-angel-notice {
    color: #666;
    padding: .2rem .22rem;
  }
  .f-marginB40 {
    margin-bottom: .4rem;
  }
}
</style>



