<template lang="html">
  <div class="foot-swipe-block">
    <div class="foot-swipe-block__mark" @click="$emit('close-swipe')"></div>
    <div class="foot-swipe-block__cnt">
      <div class="hd borderB">{{title}}</div>
      <div class="list">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: String
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.foot-swipe-block__mark {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
}
.foot-swipe-block__cnt {
  background: @whiteColor;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 0.3rem;
  color: @defaultColor;
}
.foot-swipe-block__cnt .hd {
  height: 0.88rem;
  line-height: 0.88rem;
  text-align: center;
}
</style>



